<template>
	<div class="content_wrapper">
    	<!-- <span @click='getData()'>dfsdf</span> -->
    	<div id="toplist_wrap">
    		<ul >
    			<li v-for="(item,index) in list1" class="topic_item" @click="selectItem(item)">
    				<div class="topic_main">
    					<a href="javascript:;" class="topic_media">
			    			<img :src="item.picUrl">
			    			<span class="listen_count">{{Math.floor(item.listenCount/10000)}}万</span>
			    		</a>
			    		<div class="topic_info">
			    			<div class="topic_cont">
			    				<h3 class="topic_tit">{{item.topTitle}}</h3>
			    				<p v-for="list3 in item.songList">
			    					<span>{{list3.songname}}</span>-{{list3.singername}}</p>
			    			</div>
			    		</div>
    				</div>
    			</li>
    		</ul>
    		<p style="text-align:center;background:#f4f4f4;">
    			去客户端发现更多好音乐 >
    		</p>
    	</div>
  </div>
</template>
<script>
import axios from 'axios'
	export default{
		data(){
			return {
				list1:[]
			}
		},
		mounted:function(){

	        this.getData();
	        // this.getAxios();
	        console.log('排行榜');
	        //需要执行的方法可以在mounted中进行触发，其获取的数据可以赋到data中后，可以放在前面进行渲染
    	},
		methods:{
			getData(){
				var _self = this;
				 $.ajax({
				  type: "get",
				  async: false,
				  contentType: "application/json",
				  url:"https://c.y.qq.com/v8/fcg-bin/fcg_myqq_toplist.fcg",//排行榜
				  dataType: "jsonp",
				  jsonp: "callback",
				  jsonpCallback: "MusicJsonCallback",
				  success: function(response) {
				  	console.log(response);
	  				_self.list1=response.data.topList;
				  },
				  error: function(error) {
				    console.log(error);
				  }
				});
			},
			selectItem(item){
				var _this=this;
				// console.log(_this.props)
				var id=item.id
				_this.$router.push({path:'/musiclist',query:{id}});
			}
				//_this.$router.go('/musiclist');不可以
		}
	}
</script>

<style scoped>
	ul, menu, dir {
	    display: block;
	    list-style-type: disc;
	    -webkit-margin-before: 1em;
	    -webkit-margin-after: 1em;
	    -webkit-margin-start: 0px;
	    -webkit-margin-end: 0px;
	    -webkit-padding-start: 0px;
		list-style: none;
	}
	li{
		list-style: none;
		margin-bottom: 10px;
	    overflow: hidden;
	    margin-left: 10px;
	    /*margin-top: 5px;*/
	    height: 100px;
	    margin-right: 10px;
	}
	#toplist_wrap{
		margin-top: -5px;
		background: 
	}
	.topic_main {
	    display: -webkit-box;
	    background: #fff;
	    height: 100px;
	}
	.listen_count {
		position: absolute;
	    display: block;
	    /*left: 32px;*/
	    text-align: center;
	    width: 100px;
	    bottom: 7px;
	    line-height: 12px;
	    color: #fff;
	    opacity: .6;
	    font-size: 9px;
	    z-index: 10;
	}
	.topic_media{
	    position: relative;
	    width: 100px;
	    height: 100px;
	    display: block;
	}
	.topic_media img{
	    display: block;
	    width: 100px;
	    height: 100px;
	}
	.topic_info {
	    position: relative;
	    -webkit-box-flex: 1;
	    display: -webkit-box;
	    -webkit-box-align: center;
	    -webkit-box-pack: center;
	}
	.topic_cont {
	    -webkit-box-flex: 1;
	    margin: -20px 10px 0 15px;
	    height: 100px;
	}
	.topic_tit {
	    font-size: 16px;
	    color: #000;
	    font-weight: normal;
	    margin-bottom: 5px;
	}
	.topic_cont p {
	    overflow: hidden;
	    white-space: nowrap;
	    text-overflow: ellipsis;
	    font-size: 14px;
	    color: rgba(0,0,0,.5);
	}
	.topic_cont p span{
	    color: black;
	}
</style>


