<!-- http://cache.video.iqiyi.com/jp/avlist/202861101/1/?callback=jsonp1 -->
<template>
	<div>
		<ul>
			<li>{{list.ps}}</li>
			<li v-for="result in list.vlist">
				
				<a :href="result.vurl">{{result.vn}}</a>
			</li>
		</ul>
	</div>
</template>
<script>
import axios from 'axios'
import $ from 'jquery'
const API_PROXY = 'https://bird.ioliu.cn/v1/?url=';//axios跨域
	export default{
		data(){
			return {
				list:[]
			}
		},
		created(){
			this.getList();
		},
		methods:{
			getList(){
				var _this=this;
				var url='http://cache.video.iqiyi.com/jp/avlist/202861101/jsonp2';
				// axios(API_PROXY+url).then((res)=>{
				// 	console.log(res.data.status.message.rawResponse);
				// 	var obj = eval('(' + res.data.status.message.rawResponse + ')');
				// 	console.log(obj);
				// }).catch((err)=>{

				// })

				$.ajax({
				  type: "get",
				  dataType: "jsonp",
				  url:'http://cache.video.iqiyi.com/jp/avlist/202861101/',
				  success: function(response) {
				  	console.log(response.data)
				  	_this.list=response.data;
				  },
				  error: function(error) {
				    console.log(error);
				  }
				});
			}
		}
	}
</script>
<style scoped>
*{
	padding:0;
	margin:0;
}
	li{
		list-style: none;
		text-align: center;
		line-height: 30px;
	}
</style>


