<template>
	<div class="content_wrapper">
    	<!-- 轮播 -->
    	 <div class="swiper-container">
	        <div class="swiper-wrapper">
	        	<a  v-for="img in slider" :href="img.linkUrl" style="height:100%;width:100%;display:block" class="swiper-slide">
		            <!-- <div class="swiper-slide" v-for="img in slider" :style="{ backgroundImage: 'url(' + img.picUrl + ')' }">
		            </div> -->
		            <img :src="img.picUrl">
	        	</a>
	        </div>
	        <!-- <div class="swiper-pagination swiper-pagination-white"></div> -->
	        <div class="swiper-pagination swiper-pagination-bullets"></div>
	    </div>
	    <!-- 电台 -->
	     <div class="radio_station">
	    	<h2 class="list_title">电台</h2>
	    	<ul id="radio_wrapper" class="list_container">
		        <li class="js_play_radio" v-for="radiolist in radioList">
		            <a class="list_main" href="javascript:;">
		                <div class="list_media">
		                    <img :src="radiolist.picUrl" alt="">
		                </div>
		                <div class="list_info">
		                    <h3 class="list_tit">{{radiolist.Ftitle}}</h3>
		                </div>
		            </a>
		        </li>
		    </ul>
	    </div>
	    <!-- 热门歌单 -->
	    <div class="hot_songlist">
		    <h2 class="songlist_title">热门歌单</h2>
		    <ul id="songlist_wrapper" class="songlist_container">
		        <li  v-for="songlist in songList">
		            <a class="songlist_main" >
		                <div class="songlist_media">
		                    <img :src="songlist.picUrl">
		                </div>
		                <div class="songlist_info">
		                    <h3 class="songlist_tit">{{songlist.songListDesc}}</h3>
		                    <p class="songlist_text">{{songlist.songListAuthor}}</p>
		                </div>
		            </a>
		        </li>
		    </ul>
		    <p class="list_more"><a href="javascript:;">去客户端发现更多好音乐 &gt;</a></p>
		    <p class="list_more"><a href="javascript:;">查看电脑版网页</a></p>
		</div>
  	</div>
</template>
<script>
	import axios from 'axios'
	import Swiper from 'swiper';
	import $ from 'jquery'
    import 'swiper/dist/css/swiper.min.css';
    const API_PROXY = 'https://bird.ioliu.cn/v1/?url=';//axios跨域
	export default{
		data(){
			return {
				timer: null,
				mark: 0,
				slider:[],
				radioList:[],
				songList:[],
			}
		},
		created:function(){
    		// this.getDataAxios();
    		this.getData();
    	},
		mounted:function(){
			// this.getData();
	        //需要执行的方法可以在mounted中进行触发，其获取的数据可以赋到data中后，可以放在前面进行渲染
	        console.log('推荐');
             var swiper = new Swiper('.swiper-container', {
                pagination: {
                	el:'.swiper-pagination',
                	clickable:true
                },
                loop: true,
                speed: 600,
                autoplay: 1000,
                observer:true,
                observeParents:true,
                autoplayDisableOnInteraction: false,
                onTouchEnd: function() {
                    swiper.startAutoplay()
                }
            });
    	},
    	
		methods:{
			getData(){
				var _self = this;
				//axios.get('./static/recom.json')
				axios.get(API_PROXY+'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg')
			    .then(function (response) {
			      console.log(response);
			      _self.slider=response.data.data.slider;
				  _self.radioList=response.data.data.radioList;
				  _self.songList=response.data.data.songList;;
			    })
			    .catch(function (error) {
			    console.log(error);
			    });
			}
		}
	}
</script>
<style lang="less">
	/*轮播图*/
    .swiper-container {
        width: 100%;
        height: 10rem;
        .swiper-wrapper {
            width: 100%;
            height: 100%;
        }
        .swiper-slide {
            background-position: center;
            background-size: 100% 100%;
            width: 100%;
            height: 100%;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .swiper-pagination-bullet {
            width:0.833rem;
            height: 0.833rem;
            display: inline-block;
            background: #7c5e53;
        }
    }
    ul, menu, dir {
	    display: block;
	    list-style-type: disc;
	    -webkit-margin-before: 1em;
	    -webkit-margin-after: 1em;
	    -webkit-margin-start: 0px;
	    -webkit-margin-end: 0px;
	    -webkit-padding-start: 0px;
		list-style: none;
	}
	
	/*热门歌单*/
	.hot_songlist{
		margin-top: -10px;
		margin-left: 10px;
    	margin-right: 10px;
    	.songlist_title{
		    font-size: 16px;
		    color: #000;
		    margin-bottom: 11px;
		    font-weight: normal;
		}
		.songlist_container{
			overflow: hidden;
	   		margin-right: -8px;
	   		li{
	   			float: left;
			    width: 50%;
			    -webkit-box-sizing: border-box;
			    padding-right: 8px;
			    overflow: hidden;
			    a{
			    	font-size: 14px;
			    	display: block;
					background: #fff;
					color:black;
					.songlist_media{
						position: relative;
				    	margin-bottom: 5px;
				    	img{
							position: absolute;
						    top: 0;
						    left: 0;
						    display: block;
						    width: 100%;
						    z-index: 1;
						}
						&::before{
							content: "";
						    display: block;
						    padding-top: 100%;
						}
					}
					.songlist_info{
						.songlist_tit{
							margin-top: 5px;
							overflow: hidden;
							text-overflow:ellipsis;
							white-space: nowrap;
							padding-left: 10px;
						}
						.songlist_text{
							margin-top: -10px;
							padding-left: 10px;
							padding-bottom: 15px;
						}
					}
			    }
	   		}
		}
	}

	.list_more{
		text-align: center;
		a{
			text-decoration: none;
			color: rgba(0,0,0,.6);
			font-size: 14px;
		}
	}
</style>
<style scoped>
.content_wrapper{
	/*background: #f4f4f4;*/
}
	/*电台*/
	.radio_station{
		margin-left: 10px;
    	margin-right: 10px;
	}
	.list_title{
		font-size: 16px;
	    color: #000;
	    margin-bottom: 11px;
	    font-weight: normal;

	}
	.list_container {
	    overflow: hidden;
	    margin-right: -8px;
	}
	.list_container li{
		float: left;
	    width: 50%;
	    -webkit-box-sizing: border-box;
	    padding-right: 8px;
	    margin-bottom: 10px;
	    overflow: hidden;
	}
	.list_main{
		display: block;
    	background: #fff;
    	text-decoration: none;
    font-size: 12px;
	}
	.list_media {
	    position: relative;
	    margin-bottom: 5px;
	}
	.list_media::before {
	    content: "";
	    display: block;
	    padding-top: 100%;
	}
	.list_media img {
	    position: absolute;
	    top: 0;
	    left: 0;
	    display: block;
	    width: 100%;
	    z-index: 1;
	}
	.list_info {
	    padding: 0 7px 0px;
	    color: #000;
	}
	.list_tit {
	    height: 36px;
	    line-height: 18px;
	    white-space: normal;
	    word-wrap: break-word;
	}
	/*.radio_station{
		height: 264px;
		width: 100%;
		.list_title{
			margin-left: 10px;
		    font-size: 16px;
		    color: #000;
		    margin-bottom: 11px;
		    font-weight: normal;
		}
		.list_container{
			display: flex;
			li{
				background: #fff;
				 width: 100%;
				 height: 210px;
				 margin-left: 10px;
				 margin-right:10px;
				.list_main{
					text-decoration: none;
					color:black;
					.list_media{
						padding-bottom: 15px;
						img{
							z-index: 1;
	    					width: 100%;
						}
					}
					.list_info{
						font-size: 12px;
						margin-top: -25px;
						padding-left: 10px;
					}
				}
			}
			li:last-child{
				margin-left: 0px;
			}
		}
	}
*/
</style>




