    // }).then(function (res) {
    //   // this.musics = res.data.result.tracks
    //   console.log(res)
    // }, function (error) {
    //   console.log(error)
    // })

    //3
    this.$http.get('/api/playlist/detail?id=19723756', {}, {
      headers: {

      },
      emulateJSON: true
    }).then(function (res) {
      this.musics = res.data.result.tracks
      console.log(this.musics)
    }, function (error) {
      console.log(error)
    })
  }
}
</script>

