<template>
	<div class="container">
		<div class="slider" >
			<span v-show="msg" class="uid" ref="uid">{{$route.params.id}}</span>
			<div class="img">
				<img :src="$route.params.pic">
			</div>
			<div class="song_info_flex">
				<span>{{$route.params.songname}}</span>
				<span>{{$route.params.uname}}</span>
			</div>
			<div class="song_play">
				{{(urlmp3.size/1024/1024).toFixed(2)}}MB
			</div>
		</div>
		<div class="music">
			<audio    controls autoplay id="music" ref="music"></audio>
		</div>
		<div class="lyric" >
			<div id="content">
				<p v-for="(re,index) in geci" :id="re.ct"  class="song" ref="song">
					{{re.cc}}
				</p>
			</div>
			<img :src="$route.params.pic" class="bg">
		</div>
	</div>
</template>
<script>
	import axios from 'axios';
	const API_PROXY = 'https://bird.ioliu.cn/v1/?url=';//axios跨域
	export default{
		data(){
			return {
				msg:false,
				urlmp3:'',
				geci:[],
				lyricsid:''
			}
		},mounted(){
			this.getData();
			// this.getLyric();
		},
		methods:{
			getData:function(){
				var _this=this;
				console.log("songid:",_this.$router.history.current.query.songid);
				var songid=_this.$router.history.current.query.songid;
				var mp3url="http://dl.stream.qqmusic.qq.com/C400"+songid+".m4a?guid=6940616055&vkey=70D4BBA565D4190A2EA755E32A152ECACBA6E88131BA2759030F61CDB22114F982F686AC7623ACF963030B1B54E8F9CA1118CC11F6D75013&uin=0&fromtag=38";
		       var music=this.$refs.music;

				music.src=mp3url;

				//歌词
				//https://c.y.qq.com/lyric/fcgi-bin/fcg_query_lyric.fcg?nobase64=1&musicid=213493655&songtype=0&callback=jsonp1

				//https://c.y.qq.com/lyric/fcgi-bin/fcg_query_lyric.fcg?g_tk=5381&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&nobase64=1&musicid=213493655&songtype=0&_=1524016524029&jsonpCallback=jsonp

				//https://c.y.qq.com/soso/fcgi-bin/client_search_cp?ct=24&qqmusic_ver=1298&new_json=1&remoteplace=txt.yqq.song&searchid=68118207120605907&t=0&aggr=1&cr=1&catZhida=1&lossless=0&flag_qc=0&p=100&n=20&w=多幸运&g_tk=5381&jsonpCallback=searchCallbacksong5453&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0
			},
			getLyric(){
				//https://c.y.qq.com/lyric/fcgi-bin/fcg_query_lyric.fcg?g_tk=5381&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&nobase64=1&musicid=213493655&songtype=0&_=1524016524029&jsonpCallback=jsonp

				
				var url='https://c.y.qq.com/lyric/fcgi-bin/fcg_query_lyric.fcg?g_tk=5381&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&nobase64=1&musicid=213725696&songtype=0&_=1524016785248&jsonpCallback=jsonp2';
		        axios(API_PROXY+url).then((res)=>{
		          console.log(res)
		        }).catch((err)=>{

		        })



			}



		}
	}
</script>
<style scoped>
 

	.container{
		/*position: relative;*/
		position: relative;
        z-index:6;
        /*top:-84px;*/
	}
	.slider{
		display: flex;
		position: relative;
		z-index: 16;
		top:0px;
	}
	.slider div{
		flex: 1;
		height: 80px;
		line-height: 80px;
		background: #fff;
		padding-left: 10px;
		position: relative;
		z-index: 100;
	}
	.slider div.img img{
		vertical-align: middle;
		width: 70px;
		height: 70px;
		border-radius: 50%;
	}
	.music{
		text-align: center;
		width: 100%;
		position: relative;
		z-index: 100;
	}
	.music audio{
		width: 100%;
	}
	.song_info_flex{
		overflow: hidden;
	}
	.lyric{
		/*background: linear-gradient(red, blue);  标准的语法 */
		/*width:250px;  */
	    height: 449px;
	    /*border:1px solid grey;*/
	    /*overflow-y:scroll;*/
	    overflow: hidden;
	    text-align: center;
	    position: absolute;
	    /*z-index: 100px;*/
	    z-index: 1;
	    font-weight: bold;
	    width: 100%;
	    background-size:cover contain;
	    /*opacity: 0.2;*/
	    /*background:black;*/
	    /*background-color:transparent;*/
	    /*transform: translate3d(0px, -235.294px, 0px) scale(1);*/
	    background: #222;
    	/*opacity: 0.6;*/
	
	}
	.bg{
		width: 100%;
		height: 100%;
		-webkit-filter: blur(75px); /* Chrome, Opera */
       -moz-filter: blur(75px);
        -ms-filter: blur(75px);    
            filter: blur(75px);  
            position: relative;
            z-index: -1;
	}
	#content{
		/*height: 449px;*/
		    position: absolute;
	    left: 0;
	    right: 0;
	    /*top: 213px;*/
	    top:0;
	    bottom: 0;
	    z-index: 4;
		overflow: hidden;
		margin-top: 0px;

	}
	#content p{
		margin:0 auto;
		/*overflow: hidden;*/
		transition: all 1s;
		/*position: relative;*/
		/*overflow: hidden;*/
		color:#fff;
		height: 42px;
	    line-height: 42px;
	    overflow: hidden;
	    /*text-overflow: ellipsis;*/
	    /*white-space: nowrap;*/
	    font-size: 16px;
	    /*color: rgba(255,255,255,.6);*/
	    /*margin-bottom: -0px;*/
	    /*text-align: center;*/
	    /*position:relative;*/
	    z-index: 3;
	    /*color:black;*/
	    /*background: black;*/
	    

	}

	
</style>


