<template>
  <transition>
    <div class="music-list" ref='music'>
      <div style="text-align: center;"  >
      <img src="../../static/down.svg" style="background:#a06363;color:#fff;" @click="hide">
      <span style="color:#fff;display: block;font-size: 20px;height:36px;">{{topinfo.ListName}}</span>
      </div>
      <div class="mymusic">
        <img :src="topinfo.pic_album" class="musicimg" style="">
      </div>
      <div class="songlist">
        <a style="text-decoration: none;" v-for="result,index in songlist"  href="javascript:;" @click="playSong(result.data.songmid)">
          <!-- :href="'http://y.qq.com/#type=song&mid='+result.data.songmid" -->
        <div  style="color:#fff;height: 44px;line-height:44px;padding-left: 30px;font-size: 18px;overflow:hidden;width:500px;white-space: nowrap;text-overflow: ellipsis;" >
          {{index+1}}&nbsp;&nbsp;&nbsp;
          {{result.data.albumname}}&nbsp;&nbsp;&nbsp;&nbsp;
          <span style="font-size:14px;color: hsla(0,0%,100%,.3);display: inline-block;line-height: 50px;">{{result.data.singer[0].name}}</span>
        </div>
        </a>
      </div>
      <div>
      </div>
    </div>
  </transition>
</template>

<script>
import axios from 'axios'
const API_PROXY = 'https://bird.ioliu.cn/v1/?url=';//axios跨域
