        axios(API_PROXY+url).then((res)=>{
          _self.topinfo=res.data.topinfo;
          _self.songlist=res.data.songlist;
          console.log(res.data.songlist);


        // var mp3='http://dl.stream.qqmusic.qq.com/C400000rnXvS2DFTXb.m4a?guid=5205324828&vkey=0A8894F6A60C61126D71044336B89244D64DD10BF0DE16C5B3E97B8704A5FCAA830D14BAB9F089D58A6AEC589AE259ECC387C36014A974C5&uin=0&fromtag=38';
        
        //songid songmid
        // var mp3='http://y.qq.com/#type=song&mid=002G0sJY2wThyx';
        // // var mp3='https://u.y.qq.com/cgi-bin/musicu.fcg';
        //   axios(API_PROXY+mp3).then((res)=>{
        //     console.log('res',res)

        //   }).catch((err)=>{

        //   })
        }).catch((err)=>{

        })
        // console.log(_self.$router.history.current.query.item)
      },
      playSong(songid){
        //songmid  000y22G51OtEQv
         var mp3url=`http://dl.stream.qqmusic.qq.com/C400${songid}.m4a?guid=6940616055&vkey=53FD5C1FD06EDE134E1BA04633BF72A8C1794C250C0845D8A6240D043DD95AD88D216F417F79AEE61D321DDB094D1DA3C0979239FED26A65&uin=0&fromtag=38`;
        // http://dl.stream.qqmusic.qq.com/C400'+songmid+'.m4a?guid=6940616055&vkey=53FD5C1FD06EDE134E1BA04633BF72A8C1794C250C0845D8A6240D043DD95AD88D216F417F79AEE61D321DDB094D1DA3C0979239FED26A65&uin=0&fromtag=38
        var _this=this;
        if(document.querySelector('audio')){
          document.querySelector('audio').src=mp3url;
        }else{
        var audio=document.createElement('audio');
        var music=this.$refs.music;
            audio.setAttribute('autoplay','autoplay')
            audio.setAttribute('controls','controls')
            audio.style.cssText="position:fixed;bottom:0px;width:100%;"
            audio.src=mp3url;
            music.appendChild(audio);
        }
        // console.log(_this.props)
        //var id=item.id
        //_this.$router.push({path:'/playsong',query:{songid}});
      },
      getList(){
        //https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg?g_tk=5381&uin=0&format=json&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&tpl=3&page=detail&type=top&topid=26&_=1523701513846

        //https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg?g_tk=5381&uin=0&format=json&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&tpl=3&page=detail&type=top&topid=27&_=1523702458386
        var url='https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg';
        axios(API_PROXY+url).then((res)=>{
          console.log(res)
        }).catch((err)=>{

        })
      },
      hide(){
        // num++;
        this.$refs.music.style.top=670+'px';

        //http://dl.stream.qqmusic.qq.com/C400000rnXvS2DFTXb.m4a?guid=5205324828&vkey=0A8894F6A60C61126D71044336B89244D64DD10BF0DE16C5B3E97B8704A5FCAA830D14BAB9F089D58A6AEC589AE259ECC387C36014A974C5&uin=0&fromtag=38

        // "C400000rnXvS2DFTXb.m4a?guid=5205324828&vkey=0A8894F6A60C61126D71044336B89244D64DD10BF0DE16C5B3E97B8704A5FCAA830D14BAB9F089D58A6AEC589AE259ECC387C36014A974C5&uin=0&fromtag=38"

      }
    }
  }

