<style scoped>
  .music-list{
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: #a06363;
  }
  .songlist{
    height: 100%;
    overflow-x: hidden;
    
  }
  .mymusic{
     width: 200px;
    /*height: 250px;*/
    height: 130px;
    border-radius: 50%;
    
    overflow: hidden;
    position: relative;
    left: 50%;
    margin-left: -100px;
  }
  .musicimg{
    /*position: relative;
    width: 100%;
