<template>
	<div class="content_wrapper">
    	<div class="search_wrap">
    		<div class="mod_search_bar">
    			<div id="input_wrap">
    				<form>
    					<input type="search" placeholder="搜索歌曲、歌单、专辑" 
    					class="search_input" @focus = "getFocus()"
    					v-on:keyup.enter="search" v-model="abc" >
    				</form>
    			</div>
    		</div>
    		<div class="hot_keys">
    			<h3 class="result_tit" @click='play()'>热门搜索</h3>
    			<div class="result_tags" >
				    <a href="javascript:;" class="js_keyword tag_s" v-for="hots in hotkey" :id="hots.n" @click="getData($event)">{{hots.k}}</a>
				</div>
    		</div>
    		<div class="focus_wrapper">
    			<ul>
    				<li v-show="search_result1==''? '':search_result1" >
    					<span style="display:inline-block;line-height:55px;">{{search_result1.singername}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
    					<span style="display:inline-block;line-height:55px;font-size:10px;color: #808080;">单曲&nbsp;:&nbsp;{{search_result1.songnum}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
    					<span style="display:inline-block;line-height:55px;font-size:10px;color: #808080;">专辑&nbsp;:&nbsp;{{search_result1.albumnum}}</span>
    				</li>
    				<li v-for="result in search_result" :id="result.id" @click="play(result.songmid)">
    					<!-- http://ws.stream.qqmusic.qq.com/212437019.m4a?fromtag=46 
    						'http://y.qq.com/#type=song&mid='+result.songmid-->
    					<a style="display:block;">
	    					<!-- {{result.singer}} -->
	    					<img src="../../static/yf.svg" style="height:28px;width:28px;display:inline-block;margin-top:7px;">
	    					<span  style="display:inline-block;line-height:55px;">{{result.songname}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
	    				<span v-for="r in result.singer" style="font-size:10px;color: #808080;display: inline-block;line-height: 55px;">
	    					{{r.name}}</span>

    					</a>
    				</li>
    			</ul>
    		</div>
    	</div>
  </div>
</template>
<script>
	import axios from 'axios';
	const API_PROXY = 'https://bird.ioliu.cn/v1/?url=';//axios跨域

	export default{
		data(){
			return {
				search_result:[],
				search_result1:[],
				hide:false,
				urlmp3:'',
				abc:'',
				getLocal:[],
				obj1:[],
				hotkey:[]

			}
		},
		mounted:function(){
			console.log('搜索');
			//this.search();
			// this.getFocus()
			this.hotKey();
		},
		methods:{
			play(songmid){
				var url='http://dl.stream.qqmusic.qq.com/C400'+songmid+'.m4a?guid=6940616055&vkey=53FD5C1FD06EDE134E1BA04633BF72A8C1794C250C0845D8A6240D043DD95AD88D216F417F79AEE61D321DDB094D1DA3C0979239FED26A65&uin=0&fromtag=38';
				console.log(songmid);
				var focus_wrapper=document.querySelector('.focus_wrapper')
				if(document.querySelector('audio')){
		          document.querySelector('audio').src=url;
		        }else{

				var audio=document.createElement('audio');
					audio.setAttribute('controls','controls')
					audio.setAttribute('autoplay','autoplay')
					audio.style.cssText="position:fixed;bottom:0px;width:100%;"
					audio.src=url
					focus_wrapper.appendChild(audio)
		        }

					const url1 = 'https://c.y.qq.com/api/lyric'

				  const data = Object.assign({}, {
				    g_tk: 1928093487,
				    inCharset: 'utf-8',
				    outCharset: 'utf-8',
				    notice: 0,
				    format: 'jsonp'
				  },
				  {
				    songmid: songmid,
				    platform: 'yqq',
				    hostUin: 0,
				    needNewCode: 0,
				    categoryId: 10000000,
				    pcachetime: +new Date(),
				    format: 'json'
				  })

				  // axios.get(url1, {
				  //   params: data
				  // }).then((res) => {
				  //   // return Promise.resolve(res.data)
				  //   console.log("res:",res);
				  // })
				//  $.ajax({
				//   type: "get",
				//   dataType: "jsonp",
				//   // jsonp:'jsonpCallback',
				//   jsonpCallback: "jsonp1",
				//   url:"https://c.y.qq.com/lyric/fcgi-bin/fcg_query_lyric.fcg?g_tk=5381&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&nobase64=1&musicid=202658270&songtype=0&_=1524284177780",
				//   success: function(response) {
				//   	console.log("点击获取1：",response)
				//   },
				//   error: function(error) {
				//     console.log(error);
				//   }
				// });
			},
			//热门歌曲
			hotKey:function(){
				var _this=this;
				axios.get(API_PROXY+"https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg")
				.then(function(response){
					
					var arr=response.data.data.hotkey;
					// var newArr=[];
					// for(var i=0;i<arr.length;i++){
					// 	// 随机选择一个
					// 	var randomIndex = Math.floor(Math.random() * arr.length);
					// 	 // 出列到新队伍
					// 	 console.log(randomIndex);
  			// 			newArr[i] = arr[randomIndex];
  			// 			// 出来了不能继续叫了哦
  			// 			arr.slice(randomIndex,2);
					// }
					_this.hotkey=arr;
				})
				.catch(function(error){
					console.log(error);
				})
			},
			getData(e){
				var _self = this;
				var like=e.target.innerHTML;
				var hot_keys=document.querySelector('.hot_keys');//获取热门搜索内容
				$.ajax({
				  type: "get",
				  dataType: "jsonp",
				  jsonp:'jsonpCallback',
				  url:"https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp?perpage=200&n=200&zhidaqu=1&catZhida=1&w="+like,
				  success: function(response) {
				  	console.log("点击获取：",response.data)
				  	//console.log(response.data.song.list)
				  	_self.search_result=response.data.song.list;
				  	_self.search_result1=response.data.zhida;
				  	hot_keys.style.display='none';
				  },
				  error: function(error) {
				    console.log(error);
				  }
				});
			},
			//点击输入框样式
			getFocus:function(){
				var _this=this;
				//把输入框的边框隐藏
				document.querySelector('.search_input').style.outline='none';
				//获取热门搜索的所有内容
				document.querySelector('.hot_keys').innerHTML='';
				//搜索结果显示的内容
				var focus_wrapper=document.querySelector('.focus_wrapper');
				//获取localStorage
				var hotresult = JSON.parse(localStorage.getItem("hot"));
				//
				//hide.innerHTML='';
				// focus_wrapper.innerHTML=''
				//console.log("this.obj1:",_this.obj1);
				//结果返回同步
				_this.getLocal=hotresult

			},
			//enter事件触发
			search:function(){
				var _self=this;
				var hot=document.querySelector('.search_input').value;
				var hot_keys=document.querySelector('.hot_keys');
				//存入数组 设置localStorage
				//var obj=['李白','你黑'];
				_self.obj1.push(hot);
    			localStorage.setItem("hot",JSON.stringify(_self.obj1));
				//console.log('obj1:',_self.obj1);
				console.log('hot',hot)
				// axios.post(API_PROXY+"https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp?perpage=200&n=200&zhidaqu=1&catZhida=1&w="+hot)
			 //   .then(function (response) {
			 //     console.log('response',response);
			 //      hot_keys.style.display='none';
			 //   })
			 //   .catch(function (error) {
			 //     console.log(error);
			 //   });
			 $.ajax({
				  type: "get",
				  dataType: "jsonp",
				  jsonp:'jsonpCallback',
				  url:"https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp?perpage=200&n=200&zhidaqu=1&catZhida=1&w="+hot,
				  success: function(response) {
				  	console.log(response.data.song.list)
				  	_self.search_result=response.data.song.list;
				  	_self.search_result1=response.data.zhida;
				  	hot_keys.style.display='none';
				  },
				  error: function(error) {
				    console.log(error);
				  }
				});


			}
		}
	}


				// var obj=['李白','你黑'];
				// obj.push(hot);
    // 			localStorage.setItem("hot",JSON.stringify(obj));

    // 			var hotresult = JSON.parse(localStorage.getItem("hot"));



</script>
<style scoped>
/*@import "http://at.alicdn.com/t/font_553673_m4zsc87qvi1f9a4i.css";*/

 ul, menu, dir {
	    display: block;
	    list-style-type: disc;
	    -webkit-margin-before: 1em;
	    -webkit-margin-after: 1em;
	    -webkit-margin-start: 0px;
	    -webkit-margin-end: 0px;
	    -webkit-padding-start: 0px;
		list-style: none;
	}
	/*搜索框*/
	.mod_search_bar{
		background: #f4f4f4;
	    padding: 10px;
	    display: -webkit-box;
	    -webkit-box-orient: horizontal;
	    -webkit-box-align: center;
	}
	 .search_input {
	    height: 20px;
	    line-height: 20px;
	    width: 100%;
	    color: rgba(0,0,0,.3);
	    border: none;
	    -webkit-appearance: none;
	    font-size: 14px;
	}
	#input_wrap{
		position: relative;
	    border-radius: 3px;
	    background: #fff;
	    height: 20px;
	    padding: 8px 12px 8px 35px;
	    -webkit-box-flex: 1;
	}
	.hot_keys{
	    background: #fff;
	    padding: 15px 15px 10px 15px;
	    height: 100%;
	}
	.result_tit {
	    color: rgba(0,0,0,.6);
	    margin-bottom: 8px;
	    font-size: 14px;
	}
	.tag_s {
	    display: inline-block;
	    font-size: 14px;
	    padding: 0 10px;
	    height: 30px;
	    line-height: 30px;
	    color: #000;
	    border: 1px solid rgba(0,0,0,.6);
	    border-radius: 99px;
	    word-break: keep-all;
	    margin-bottom: 10px;
	    margin-right: 14px;
	    text-decoration: none;
	}
	.focus_wrapper{
		/*background: #fff;*/
		margin-top: -17px;

	}
	li{
		/*list-style: none;
		height: 55px;
		background: #fff;
		margin-top: 2px;
		margin-bottom: 1px;
		line-height: 55px;
		padding-left: 10px;
		display: flex;*/
		position: relative;
	    height: 55px;
	    padding-left: 56px;
	    overflow: hidden;
	    background: #fff;
	        /*margin-left: -40px;*/
	}
	li::after {
    content: "";
    position: absolute;
    height: 1px;
    top: 0;
    left: 0;
    right: 0;
    background: #e5e5e5;
}
	li img{
		position: absolute;
	    top: 8px;
	    left: 15px;
	    width: 40px;
	    height: 40px;
	    background-position: 0 0;
	    border-radius:50%;
	}
	a{
		text-decoration: none;
		color:black;
	}
	.songname{
		margin: 10px 15px 2px;
	    line-height: 18px;
	    font-size: 16px;
	    font-weight: normal;
	    color: #000;
		
	}
	.hide p{
		/*position: absolute;*/
		/*top:20px;*/
		font-size: 13px;
		margin:2px 15px 2px;
		color: #808080;
		display: inline-block;
	}
</style>



  


