import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Header from '@/components/Header'
import kuayu from '@/components/kuayu'
import TopList from '@/components/TopList'
import Recom from '@/components/Recom'
import Search from '@/components/Search'
import MusicList from '@/components/MusicList'
import Video from '@/components/Video'
import PlaySong from '@/components/PlaySong'
import VueResource from 'vue-resource'//vue-resource的写法 + php curl 完成代理请求
Vue.use(Router)
Vue.use(VueResource)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'recom',
      component: Recom
    },
    {
      path: '/header',
      name: 'Header',
      component: Header
    },
    {
      path: '/toplist',
      name: 'TopList',
      component: TopList
    },
    {
      path: '/recom',
      name: 'Recom',
      component: Recom
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/musiclist',
      name: 'MusicList',
      component: MusicList
    },
    {
      path: '/video',
      name: 'Video',
      component: Video
    },
    {
      path: '/playsong',
      name: 'PlaySong',
      component: PlaySong
    }
  ]
})



